<template>
  <v-container pa-0 ma-0
    fluid
    fill-height 
  >
  
    <MsgBox :msgObj="message" /> 

    <v-row
      align="center"
      justify="center"
    >
      <!-- cols="12" sm="8" md="4" -->
      <v-col 
      >

    actor : {{ item.actor }}<br />
    sub_genre : {{ item.sub_genre }}<br />
    kb_kmdb_genre : {{ item.kb_kmdb_genre }}<br />
    kb_kmdb_keyword : {{ item.kb_kmdb_keyword }}<br />
    <br />
            <!-- v-if="item.refine_kwd" -->
            <v-form ref="frmItem" 
            >
              <v-text-field
                label="content_keyword"
                name="content_keyword"
                type="text"

                v-model="item.refine_kwd.content_keyword"
              ></v-text-field>
              <v-text-field
                label="keyword"
                name="keyword"
                type="text"

                v-model="item.refine_kwd.keyword"
              ></v-text-field>
              <v-text-field
                label="genre"
                name="genre"
                type="text"

                v-model="item.refine_kwd.genre"
              ></v-text-field>
              <v-text-field
                label="seasons"
                name="seasons"
                type="text"

                v-model="item.refine_kwd.seasons"
              ></v-text-field>
              <v-text-field
                label="theme"
                name="theme"
                type="text"

                v-model="item.refine_kwd.theme"
              ></v-text-field>
            </v-form>
      </v-col>
    </v-row>
    <v-row
      align="center"
      justify="center"
    >
      <!-- cols="12" sm="8" md="4" -->
      <v-col 
      >
            <v-btn color="red" 
              @click="del">삭제</v-btn>
      </v-col>
      <v-col align="end"
      >
            <v-btn color="#666666" class="mr-2"
              @click="cancel">취소</v-btn>

            <v-btn color="primary" 
              @click="save"
            >저장</v-btn>
      </v-col>
    </v-row>

        <v-card 
          :loading="loading"
          v-if="false"
          >
          <v-toolbar dense
            color="red"
            flat
          >
            <v-toolbar-title>상세</v-toolbar-title>
            <v-spacer></v-spacer>

          </v-toolbar>
          <v-card-text>
<!--     
              <v-text-field
                label="아이디"
                name="login"
                prepend-icon="mdi-account"
                type="text"

                v-model="user.userId"
                @keyup.enter="$refs.txtPassword.focus()"
              ></v-text-field>

              <v-text-field
                id="password"
                label="비밀번호"
                name="password"
                prepend-icon="mdi-lock"
                type="password"

                v-model="user.password"

                @keyup.enter="login"
                ref="txtPassword"
              ></v-text-field> -->
          </v-card-text>
          <v-card-actions>
            <v-btn color="#666666" 
              @click="del">삭제</v-btn>

            <v-spacer></v-spacer>
            <v-btn color="#666666" 
              @click="cancel">취소</v-btn>
            <v-btn color="primary" 
              @click="save"
              :disabled="loading" 
              :loading="loading"
            >저장</v-btn>

          </v-card-actions>
        </v-card>

  </v-container>
</template>

<script>
class KmdbKwdItem {
  constructor(sid) {
    this.series_id = sid;

    this.content_keyword = '';
    this.keyword = '';
    this.genre = '';
    this.seasons = '';
    this.theme = '';
  }
}
  export default {
    name: 'KmdbKwdItemDetail',
    components: {
    },

    props: {
      'item' : Object,
    },
    data: () => ({
      loading: false,
      // item: new KmdbKwdItem(),
      message: {},
    }),
    watch: { 
      item: function() {
// console.log('this.item.refine_kwd', this.item.refine_kwd)
        if (!this.item.refine_kwd) {
          this.item.refine_kwd = new KmdbKwdItem()
        }
      },
    },
    computed: {
    },

    created () {
        if (!this.item.refine_kwd) {
          this.item.refine_kwd = new KmdbKwdItem()
        }
    },
    mounted() {
    },
    methods: {
      del() {
        const sid = this.item.refine_kwd.series_id
        if(confirm('delete? ' + sid)) {
          this.$axios.delete(
            '/api/kmdbKwd?sid=' + sid, 
          )
          .then(() => {
            this.item.refine_kwd = new KmdbKwdItem()
            this.message = {msg : sid + ' has deleted'}
          })
          .catch(err => {
            console.error('kmdbKwd delete', err)
          })
        }
      },
      cancel() {
        //TODO cancel
        alert('cancel')
      },
      save() {
        const saveItem = this.item.refine_kwd

        let promSave = null
        if (this.item.refine_kwd.series_id) {
          // alert('update')
          promSave = this.$axios.put(
            '/api/kmdbKwd', saveItem
          )
        }
        else {
          // alert('insert')
          saveItem.series_id = this.item.series_id
          promSave = this.$axios.post(
            '/api/kmdbKwd', saveItem
          )
        }

        promSave.then(() => {
          this.message = {msg : saveItem.series_id + ' has saved'}
        })
        .catch(err => {
          console.error('kmdbKwd save', err)
        })
      }
    },
  }
</script>