<template>
  <v-container 
  >
  <v-card>
    <v-card-title>
      지식 타입: KMDB
      <v-spacer></v-spacer>
      <v-text-field
        v-model="search"
        append-icon="mdi-magnify"
        label="검색"
        single-line
        hide-details
      ></v-text-field>

<!-- 
      top
 -->
    <v-menu
      open-on-hover
      offset-y
      ava
    >
      <template v-slot:activator="{ on, attrs }">
        <v-btn
          color="primary"
          dark
          v-bind="attrs"
          v-on="on"
        >
          <v-icon>mdi-filter</v-icon>
          필터
        </v-btn>
      </template>

      <v-list>
        <!-- <v-list-item
          v-for="(item, index) in items"
          :key="index"
        >
          <v-list-item-title>{{ item.title }}</v-list-item-title>
        </v-list-item> -->
        <v-list-item @click="filterResult()">
          <v-list-item-title>모두 표시
          </v-list-item-title>
        </v-list-item>
        <v-list-item @click="filterResult('noKwd')">
          <v-list-item-title>
            <v-icon>mdi-cancel</v-icon>키워드 없음
          </v-list-item-title>
        </v-list-item>
        <v-list-item @click="filterResult('onlyKwd')">
          <v-list-item-title>
            <v-icon>mdi-checkbox-marked-circle</v-icon>키워드 있음
          </v-list-item-title>
        </v-list-item>
      </v-list>
    </v-menu>
    </v-card-title>


    <v-data-table
      :headers="headers"
      :items="kwdList"
      :search="search"

      sort-by="series_id"
      :expanded.sync="expanded"
      show-expand
      :loading="isLoading"
      loading-text="조회 중... 잠시만 기다려 주세요."
    >

      <template v-slot:item.title="{ item }">
        {{item.title}}
        {{item.subTitle ? '(' + item.subTitle + ')' : ''}}
      </template>
      <template v-slot:item.actor="{ item }">
        {{item.actor | top3}}
      </template>

      <template v-slot:expanded-item="{ headers, item }">
        <td :colspan="headers.length">
          <KmdbKwdDetail :item="item"/>
        </td>
      </template>
      
      <template v-slot:no-data>
        조회 결과 없음
      </template>
      <template v-slot:no-results>
        검색 결과 없음
      </template>
    </v-data-table>
    
  </v-card>
  </v-container>

</template>

<script>
  import KmdbKwdDetail from '@/components/kmdb/KmdbKwdDetail'

  export default {
    name: 'KmdbKwd',
    props: {
    },
    components: {
      KmdbKwdDetail
    },
    data: () => ({
        search: '',
        headers: [
          { text: 'SERIES ID', value: 'series_id',
            width: 110 
          },

          {
            text: '제목',
            align: 'start',
            sortable: true,
            value: 'title',
            width: 300
          },
          
          { text: 'COUNTRY', value: 'country_str',
            width: 150
          },
          
          { text: 'DIRECTOR', value: 'director_str',
            width: 150
          },
          
          { text: 'ACTOR', value: 'actor',
            width: 150
          },
          
          // { text: 'Actions', value: 'actions', 
          //   sortable: false, align: 'center', 
          // },

          { text: '', value: 'data-table-expand' },
        ],
        kwdList: [],
        kwdListOrg: [],
        isLoading: false,

        expanded: [],
    }), 
    watch: {
    },

    filters: {
      top3: function (value) {
        return value.split(',').slice(0, 3).join(',')
      }
    },

    created () {
      this.refreshList()
    },
    mounted() {
    },
    methods: {
      refreshList () {
        this.isLoading = true
        this.$axios.get(
          // '/api/kmdbKwd'
          '/test-data/kmdbKwd_response.json'
        )
        .then(res => {
console.log('res_data: ', res.data)
          this.kwdList = res.data
          this.kwdListOrg = Object.assign([], this.kwdList)

          this.isLoading = false
        })
        .catch(err => {
          console.error('kwdList', err)
          this.isLoading = false
        })
      },

      filterResult(filter) {
        if ('noKwd' == filter) {
          this.kwdList = this.kwdListOrg.filter(v => {
            return !v.refine_kwd
          })
        }
        else if ('onlyKwd' == filter) {
          this.kwdList = this.kwdListOrg.filter(v => {
            return v.refine_kwd
          })
        }
        else {
          this.kwdList = Object.assign([], this.kwdListOrg)
        }
console.log('kwdList', this.kwdList)
      }
    },
  }
</script>

<style scoped>
</style>